.editCon {
    width: 100%;
    height: 100vh;
    position: fixed;
    z-index: 10;
    top: 0;
    right: 0;
    background-color: rgb(0, 0, 0, 0.8);
    display: grid;
    place-content: center;

    .cancel {
        width: 40px;
        margin-bottom: 5px;
        border: none;
        background-color: rgb(0, 0, 0, 0.07);
        color: white;
        margin-left: 86%;
    }

    .editdata {
        width: 80%;
        background-color: white;
        font-size: 12px;
        border-radius: 10px;
        .imgsection {
            img {
                width: 200px;
                height: auto;
                margin-top: 30px;
                margin-left: 35px;
                border-radius: 4px;
            }
            #file {
                margin-top: 15px;
                margin-left: 63px;
            }
        }

        .data {
            p {
                margin-top: 10px;
                font-weight: 500;
            }

            input {
                width: 290px;
                line-height: 35px;
                background-color: #f0f0f0;
                border: none;
                border-radius: 4px;
                padding-left: 20px;
                font-size: 12px;
            }

            .price {
                width: 300px;
                input {
                    width: 130px;
                }
            }

            textarea {
                display: block;
                background-color: #f0f0f0;
                border: none;
                border-radius: 5px;
                padding: 5px;
            }
            .btn {
                margin-top: 20px;
                margin-bottom: 20px;
                margin-left: 5px;
                margin-right: 5px;
                width: 144px;
                font-size: small;
            }
        }
    }
}

p.input-label-edit {
    margin: 5px 0;
}

.specialist-edit-services-card-ul {
	margin-top: 10px;
    display: flex;
	flex-wrap: wrap;
    list-style-type: none;
	// white-space:nowrap;
    // padding-left: unset;
    li {
        margin: 0 5px;
		border: 1px solid rgb(37, 37, 37);
		padding: 2px 4px;
		// display:inline;
		border-radius: 2px;
		box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.3);
    }
}

.input-wrapper-edit-specialists{
	// border: 1px solid black;
	display: flex;
	justify-content: space-between;
}
.add-btn-edit-specialist{
	transform: scale(0.5);
}
.service-input{
	width: 80% !important;
}

@media (max-width: 768px) {
    .editCon {
        .cancel {
            margin-left: auto;
        }
    }

    .editdata {
        width: 95vw !important;

        .imgsection {
            display: grid;
            place-content: center;
        }

        .data {
            display: grid;
            place-content: center;
        }
    }
}
